button#person-b {
  outline-color: #636145;
  left: calc( 1px * var(--person-b-x,0) );
  top: calc( 1px *var(--person-b-y,0) );
  width: calc( 1px * var(--person-b-w,0) );
  height: calc( 1px * var(--person-b-h,0) );
}

#person-a-controls {
  left: 0;
}

#person-b-controls {
  right: 0;
}

fieldset.person-controls {
  position: fixed;
  top: 0;
  max-width: 50%;
  bottom: 0;
  overflow: auto;
}
fieldset.person-controls label {
  color: #262422;
  background-color: rgba(231, 219, 213, 0.9);
}
fieldset.person-controls label:hover {
  color: #e7dbd5;
  background-color: rgba(38, 36, 34, 0.9);
}

button#button-record,
button#button-midi,
button.install-app,
button.update-available {
  transition: background-color 40ms linear, color 40ms linear;
  overflow: hidden;
}

button#button-video {
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 0;
  border: 0;
  z-index: -1;
  color: transparent;
  cursor: default;
  background-color: transparent;
  max-width: none;
}
button#button-video:hover, button#button-video:focus {
  background-color: transparent;
}

button#button-midi {
  --icon-size:36px;
  background-image: url(assets/logos/midi2.0.svg);
}
.midi-active button#button-midi {
  background-color: #ab8f54;
}

.midi-no-devices button#button-midi {
  background-color: #ab8f54;
  overflow: visible;
}
.midi-no-devices button#button-midi:after {
  display: block;
  content: "Connect a device";
  position: absolute;
  bottom: calc(100% + var(--pad) );
  text-indent: 0;
  left: 50%;
  text-align: center;
  transform: translateX(calc(-50% ));
  color: #262422;
  background-color: #e7dbd5;
  font-size: 11px;
  font-weight: 900;
  padding: var(--pad);
  animation: TextColours 4s linear infinite 0s both;
}

main.midi-activated #button-midi {
  cursor: not-allowed;
  --col: $col-blue;
}

button#button-record {
  background-color: #8d3525;
}
label[for=select-camera],
#select-camera {
  --col-hover:#636145;
}

button#button-photograph {
  --col-hover:#455b63;
  background-image: url(./assets/icons/add_a_photo-24px.svg);
}

button#button-duet {
  --col-hover:#ab8f54;
  background-image: url(./assets/icons/person_add-24px.svg);
  display: none;
}

button#button-solo {
  --col-hover:#ab8f54;
  background-image: url(./assets/icons/person-24px.svg);
  display: none;
}

button#share {
  background-image: url(./assets/icons/share-24px.svg);
  margin-left: auto;
}

@keyframes Hide-Install {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(calc( -100% + 44px ));
  }
}
#camera {
  background-image: url(./assets/icons/switch_video-24px.svg);
}
#camera:before {
  pointer-events: none;
}
#camera:hover, #camera:focus, #camera:target, #camera:focus-visible {
  outline: none;
}
#camera select {
  order: -1;
  cursor: pointer;
  border: 0;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  color: transparent;
  background-color: transparent;
  text-align: center;
}
#camera select optgroup {
  appearance: none;
  background-color: #ab8f54;
  color: #262422;
  font-weight: 900;
  text-transform: uppercase;
  padding: var(--pad);
  margin: auto;
}
#camera select option {
  padding: var(--pad);
  color: #262422;
  background-color: #9d8272;
  cursor: pointer;
  text-transform: initial;
}
#camera select option:hover, #camera select option:focus {
  color: #9d8272;
  background-color: #262422;
}

#foot {
  --spacer:0;
  position: absolute;
  left: calc( var(--pad));
  bottom: var(--pad);
  color: #5d493c;
  transition: transform 450ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transform: translateX(var(--spacer));
  z-index: -1;
}

@media (orientation: landscape) {
  .loaded #foot {
    transform-origin: bottom left;
    transform: rotate(270deg) translateX(67px) translateY(10px);
  }
}
@keyframes BGColours {
  0% {
    background-color: #66a329;
  }
  10% {
    background-color: #29a3a3;
  }
  20% {
    background-color: #29a3a3;
  }
  30% {
    background-color: #6629a3;
  }
  40% {
    background-color: #6629a3;
  }
  50% {
    background-color: #a32929;
  }
  70% {
    background-color: #a32929;
  }
  80% {
    background-color: #66a329;
  }
  100% {
    background-color: #66a329;
  }
}
@keyframes TextColours {
  0% {
    color: #66a329;
  }
  10% {
    color: #29a3a3;
  }
  20% {
    color: #29a3a3;
  }
  30% {
    color: #6629a3;
  }
  40% {
    color: #6629a3;
  }
  50% {
    color: #a32929;
  }
  70% {
    color: #a32929;
  }
  80% {
    color: #66a329;
  }
  100% {
    color: #66a329;
  }
}
@keyframes Fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes Blink {
  0% {
    opacity: 0;
  }
  49% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}
@keyframes Loading {
  0% {
    transform: translateX(0);
    background-image: linear-gradient(90deg, #66a329, #66a329);
  }
  10% {
    background-image: linear-gradient(90deg, #29a3a3, #29a3a3);
  }
  20% {
    transform: translateX(calc(100% - var(--loader-size)));
    background-image: linear-gradient(90deg, #29a3a3, #29a3a3);
  }
  30% {
    background-image: linear-gradient(90deg, #6629a3, #6629a3);
  }
  40% {
    transform: translateX(calc(100% - var(--loader-size))) translateY(calc( 100% - var(--loader-size) ));
    background-image: linear-gradient(90deg, #6629a3, #6629a3);
  }
  50% {
    background-image: linear-gradient(90deg, #a32929, #a32929);
  }
  70% {
    transform: translateX(0) translateY(calc( 100% - var(--loader-size) ));
    background-image: linear-gradient(90deg, #a32929, #a32929);
  }
  80% {
    background-image: linear-gradient(90deg, #66a329, #66a329);
  }
  100% {
    transform: translateX(0);
    background-image: linear-gradient(90deg, #66a329, #66a329);
  }
}
* {
  scrollbar-width: thin;
  scrollbar-color: #262422 #9d8272;
}

*::-webkit-scrollbar {
  width: 12px;
}

*::-webkit-scrollbar-track {
  background-color: #262422;
}

*::-webkit-scrollbar-thumb {
  background-color: #262422;
  border-radius: 99px;
  border: 3px solid #a45c1e;
}